.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 40%;
		left: 0;
		width: 360px;
		transform: translate(-70%, -40%);
		.orn-2 {
			position: absolute;
			top: 0;
			right: 10%;
			width: 100px;
			transform: translate(-10%, 0);
		}
	}
	.orn-3 {
		position: absolute;
		top: 40%;
		right: 0;
		width: 420px;
		transform: translate(60%, -50%);
		.orn-4 {
			position: absolute;
			top: 50%;
			left: 25%;
			width: 100px;
			transform: scaleX(-1);
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 200px;
			transform: translate(30%, 20%) scaleX(-1);
		}
	}
	.orn-6 {
		position: absolute;
		top: 70%;
		left: 0;
		width: 200px;
		transform: translate(-40%, -50%);
		.orn-7 {
			position: absolute;
			top: 0;
			right: 10%;
			width: 80px;
			transform: translate(-50%, -40%) rotate(15deg);
		}
	}
	.appology-wrap {
		position: relative;
	}
}
