.story-section {
	position: relative;
	.orn-1 {
		position: absolute;
		top: 1rem;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0);
		z-index: 3;
		.orn-2 {
			position: absolute;
			top: 0;
			right: 0;
			width: 100px;
			transform: translate(-70%, 0);
		}
	}
	.orn-3 {
		position: absolute;
		top: 1rem;
		right: 0;
		width: 200px;
		transform: translate(50%, 0) scaleX(-1);
		z-index: 3;
		.orn-4 {
			position: absolute;
			top: 40%;
			right: 0;
			width: 100px;
			transform: translate(-30%, 0);
		}
	}
	.story {
		position: relative;
		padding: 5rem 0 0;
		margin-bottom: -5rem;
		z-index: 5;
		@include max-md {
			padding: 3rem 0 0;
			margin-bottom: -3rem;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 7rem 2rem 2rem;
		background-color: var(--color-primary);
		border-radius: 300px 300px 20px 20px;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/4;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
